<template>
	<view class="flex flex-direction bg-white">
		<!-- 判断是否登录 -->
		<is-login currentPage="index"></is-login>
		<!-- 导航栏 -->
		<cu-custom bgColor="bg-cyan" :isBack="preview">
			<block slot="content">设备</block>
		</cu-custom>
		<luyj-search-bar placeholder="请输入设备名称或SN码" radius="80" cancelColor="#999999" @input="search_input"
			@cancel="search_cancel" :value="searchKey"></luyj-search-bar>
		<view class="cu-form-group margin-bottom-sm">
			<uni-data-picker placeholder="请选择产品分类" style="width: 50%;" :value="categoryId" :preload="true"
				:localdata="category_list" popup-title="产品分类" @nodeclick="changeCategory"></uni-data-picker>
			<uni-data-picker placeholder="请选择型号" style="width: 50%;" :value="modelId" :preload="true"
				:localdata="model_list" popup-title="型号" @nodeclick="changeModel"></uni-data-picker>
		</view>
		<uni-list>
			<!-- 设备列表 -->
			<uni-list-item v-for="(item , index ) in product_list" :key="index" showArrow
				:thumb="item.ImgUrl? item.ImgUrl : '/static/thumb/product_thumb.png'" thumbSize="lg"
				:to="'/pages/basic/product/product/productdetail?preview=true&isEdit=true&ProductId=' +item.ProductId ">
				<teleport slot="body" class="flex flex-direction">
					<view class=" cf ">
						<view class="fl text-cut text-lg">
							<text class="text-bold">{{ item.Name ? item.Name :'' }}</text>
						</view>
						<view class="text-sm fr cu-tag text-cut">{{ item.CategoryName ? item.CategoryName : '' }}</view>
					</view>
					<view class="text-sm text-cut text-grey">SN码：{{ item.ProductSn ? item.ProductSn :'' }}</view>
					<view class="text-sm text-cut text-grey">供应商：{{ item.SupplierMchName ? item.SupplierMchName :'' }}
					</view>
					<view class="text-sm text-cut text-grey">所属门店：{{ item.MchName ? item.MchName : '' }}</view>
				</teleport>
			</uni-list-item>
			<!-- 没有更多数据了 -->
			<uni-load-more :status="status" @clickLoadMore="productlist_request"></uni-load-more>
		</uni-list>

		<!-- 悬浮按钮(暂时不显示) -->
		<navigator v-if="false" url="/pages/basic/product/product/productdetail?preview=true&isEdit=false">
			<uni-fab ref="fab" :pattern="pattern" horizontal="left" vertical="bottom" direction="horizontal" />
		</navigator>
	</view>
</template>

<script>
	/**
	 * 设备主页
	 * @author 陆彦捷
	 * @date 2021.06.30 设备的主页样式
	 * @date 2021.07.01 设备的主页样式
	 * @date 2021.07.05 设备页样式修改
	 * @date 2021.07.09 输入框数据的修改
	 * @date 2021.07.15 简单的数据获取 + 滚动插件
	 * @date 2021.07.23 重新引用login
	 */
	import organ from '@/common/storage/organstorage.js'; // 当前组织
	import categories from '@/common/data/category.js'; // 产品分类（模拟值）
	import models from '@/common/data/model.js'; // 型号（模拟值）
	import products from '@/common/data/product.js'; // 设备(模拟值)
	import changeobj from '@/common/function/changeobjectecordname.js'; // 格式化数组/对象

	var _self;
	export default {
		data() {
			return {
				// 样式参数
				preview: false,
				pattern: {
					color: '#7A7E83', // 文字默认颜色
					selectedColor: '#00BEB7',
					backgroundColor: '#fff',
					buttonColor: '#00BEB7'
				}, // 可选配置项样式（悬浮按钮）
				// 系统参数
				MchId: 0, // 组织Id(参数的组织id或引入的组织id)
				Token: this.global.login.getToken(), // Token值
				category_list: [], // 产品分类列表
				model_list: [], // 产品型号列表

				// 系统参数 - 设备
				searchKey: '', // 关键字 (搜索条件）
				PageIndex: 1, // 当前页 
				PageSize: 10, // 每页大小
				categoryId: 0, // 产品分类id(搜索条件)
				modelId: 0, // 型号id(搜索条件)  → modelId改为vueId
				model: '', // 型号
				status: 'more', // 	加载状态
				product_list: [], // 设备列表

			}
		},
		onLoad: function(options) {
			_self = this;
			this.getParams(options);
			this.categories_request(); // 请求产品分类
			this.productlist_request(); // 设备请求(获取设备名称)
		},
		/**
		 * 上拉触底事件-加载设备信息
		 */
		onReachBottom:function(){
			this.productlist_request();
		},
		methods: {
			// ============================================ 初始化 ================================================
			/** 初始化参数
			 * @param {Object} options
			 */
			getParams: function(options) {
				this.preview = options.preview != 'false' ? Boolean(options.preview) : false; // 是否返回上一页
				this.MchId = options.MchId ? options.MchId : organ.getIndustryId(); // 组织Id
				this.searchKey = options.searchKey ? options.searchKey : ''; // 搜索值
			},
			// ============================================ 监听事件 ===============================================
			/** 设备搜索事件
			 * @param {Object} e
			 */
			search_input: function(e) {
				console.log('搜索设备事件', e);
			},
			/** 搜索设备取消事件
			 * @param {Object} e
			 */
			search_cancel: function(e) {
				console.log("搜索设备取消事件", e);
			},
			/** 修改产品分类 (点击时修改)
			 * @param {Object} item
			 */
			changeCategory: function(item) {
				this.categoryId = item.value;
				this.models_request(item.value);
			},
			/** 修改型号
			 * @param {Object} item
			 */
			changeModel: function(item) {
				this.modelId = item.value;
			},
			// ============================================ 请求事件 ===============================================
			/**
			 * 产品分类列表请求(模拟)
			 */
			categories_request: function() {
				setTimeout(function() {
					_self.category_list = changeobj.change(categories.list, "CategoryId", "Name", "Children");
					var temp = {
						"value": 0,
						"text": "无",
					};
					_self.category_list.unshift(temp);
				}, 3000);
			},
			/**
			 * 产品型号列表请求（模拟）
			 * @param {int} 产品分类 
			 */
			models_request: function(CategoryId) {
				setTimeout(function() {
					if (CategoryId == 3) {
						_self.model_list = changeobj.change(categories.list, "ModelId", "Name");
						var temp = {
							"value": 0,
							"text": "全部",
						};
						_self.model_list.unshift(temp)
					} else {
						_self.model_list = [{
							"value": 0,
							"text": "全部"
						}];
					}
				}, 3000);
			},
			/**
			 * 设备列表请求
			 */
			productlist_request: function() {
				if (this.status != "more") {
					return;
				}
				this.status = "loading";
				uni.request({
					// mchId=5&productSn=&searchKey=&categoryId=0&model=&vueId=&pageIndex=1 条件暂时省略
					url: _self.website + '/api/SvcProduct/Page?mchId=' + _self.MchId + '&pageIndex=' + _self
						.PageIndex + '&pageSize=' + _self.PageSize,
					header: {
						Authorization: 'Bearer ' + _self.Token
					},
					method: 'GET',
					success: function(result) {
						console.log("设备请求success", result);
						if (result.statusCode == 200) {
							if (result.data.ResultCode === "0") {
								try {
									var data = result.data.Data;
									if (_self.PageIndex === 1) {
										_self.product_list = data;
									} else {
										_self.product_list = _self.product_list.concat(data);
									}
									// 获取页面状态并添加索引
									if (data.length === _self.PageSize) {
										_self.status = "more";
										_self.PageIndex++;
									} else {
										_self.status = "noMore";
									}
									return;
								} catch (e) {
									console.log("设备请求success ,catch=>", e);
								}

							}
						}
						uni.showToast({
							title: '服务器连接失败',
							icon: 'none'
						});
					},
					fail: function(errors) {
						console.log("设备fail=>", errors);
						_self.status = "more";
						uni.showToast({
							title: '系统错误',
							icon: 'none'
						});
					}
				})
			},
			// =====================================================================================================
		}
	}
</script>

<style>

</style>
